import { createMuiTheme } from '@material-ui/core/styles'

import Cache from './Cache'
import Pro from './Pro'
import Settings from './Settings'
const _merge = require('deepmerge');

const colors = {
	red: require('@material-ui/core/colors/red').default,
	pink: require('@material-ui/core/colors/pink').default,
	purple: require('@material-ui/core/colors/purple').default,

	deepPurple: require('@material-ui/core/colors/deepPurple').default,
	indigo: require('@material-ui/core/colors/indigo').default,
	blue: require('@material-ui/core/colors/blue').default,

	lightBlue: require('@material-ui/core/colors/lightBlue').default,
	cyan: require('@material-ui/core/colors/cyan').default,
	teal: require('@material-ui/core/colors/teal').default,

	green: require('@material-ui/core/colors/green').default,
	lightGreen: require('@material-ui/core/colors/lightGreen').default,
	lime: require('@material-ui/core/colors/lime').default,

	yellow: require('@material-ui/core/colors/yellow').default,
	amber: require('@material-ui/core/colors/amber').default,
	orange: require('@material-ui/core/colors/orange').default,

	deepOrange: require('@material-ui/core/colors/deepOrange').default,
	brown: require('@material-ui/core/colors/brown').default,
	grey: require('@material-ui/core/colors/grey').default,

	blueGrey: require('@material-ui/core/colors/blueGrey').default
};


export default function generateTheme() {
	
	// default colors
	let themeColors = {
		'dark': '#424242', // https://material-ui.com/customization/default-theme/?expand-path=$.palette
		'primary': colors.blue[700],
		'secondary': colors.pink[500]
	};
	
	// user defined colors
	const userTheme = {
		'primary': Settings.getSetting('themeColorPrimary'),
		'secondary': Settings.getSetting('themeColorSecondary'),
		...(Pro.isPro() && Settings.getSetting('theme')) || {}
	};
	
	// adapt user defined colors
	['primary', 'secondary'].forEach(key => {
		userTheme[key] = userTheme[key] || '';
		
		if (userTheme[key].indexOf('|') > -1) {
			let [ hue, shade ] = userTheme[key].split('|');
			themeColors[key] = (colors[hue] && colors[hue][shade]) || themeColors[key];
		}
		else {
			themeColors[key] = userTheme[key].indexOf('#') > -1 ? userTheme[key] : themeColors[key];
		}
	});
	
	// get dark mode
	const darkMode = Settings.getSetting('themeDarkMode');

	// set color on mobile device
	document.getElementById('theme-color').setAttribute('content', darkMode ? themeColors.dark : themeColors.primary);

	// theme
	const theme = createMuiTheme(_merge({
		palette: {
			type: darkMode ? 'dark' : 'light',
			background: {
				main: darkMode ? themeColors.dark : themeColors.primary
			},
			primary: {
				main: themeColors.primary 
			},
			secondary: {
				main: themeColors.secondary 
			}
		},
		components: {
			icon: {
				fontSize: '24px',
				color: themeColors.primary
			},
			finger: {
				cursor: 'pointer'
			}
		}
		
	}, userTheme));

	// custom css (pro)
	let styles = `
		body { background-color: ${theme.palette.background.default} }
		
		a {
			color: ${theme.palette.text.secondary};
		}
		
		code {
			padding: .2em .4em;
			margin: 0;
			font-size: 85%;
			background-color: ${theme.palette.background.default};
			border-radius: 6px;
		}
		
		.blink {
			animation: blinker 1s linear infinite;
		}
		@keyframes blinker {
			50% { opacity: 0; }
		}
	`

	if (Pro.isPro()) {
		styles += Cache.get('styles');
	}
	
	let styleElement = document.getElementById('customStyles');
	if (!styleElement) {
		styleElement = document.createElement('style', { 'id': 'customStyles' });
	}
	
	styleElement.appendChild(document.createTextNode(styles));
	document.getElementsByTagName('head')[0].append(styleElement);
	
	return theme;
}
